import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  TextField,
  Button,
  Paper,
  Card,
  CardContent,
  Snackbar,
  Alert,
} from '@mui/material';
import EmailIcon from '@mui/icons-material/Email';
import PhoneIcon from '@mui/icons-material/Phone';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import AccessTimeIcon from '@mui/icons-material/AccessTime';

const ContactPage = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: '',
  });
  const [errors, setErrors] = useState({});
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success',
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
    // Clear error when user types
    if (errors[name]) {
      setErrors({
        ...errors,
        [name]: '',
      });
    }
  };

  const validateForm = () => {
    const newErrors = {};
    if (!formData.name.trim()) {
      newErrors.name = 'Name is required';
    }
    if (!formData.email.trim()) {
      newErrors.email = 'Email is required';
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      newErrors.email = 'Email is invalid';
    }
    if (!formData.subject.trim()) {
      newErrors.subject = 'Subject is required';
    }
    if (!formData.message.trim()) {
      newErrors.message = 'Message is required';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (validateForm()) {
      try {
        // In a real app, this would send data to the backend
        // await axios.post('/api/contact', formData);
        
        // For demo purposes, just simulate a successful submission
        setSnackbar({
          open: true,
          message: 'Your message has been sent! We will get back to you soon.',
          severity: 'success',
        });
        
        // Reset form
        setFormData({
          name: '',
          email: '',
          subject: '',
          message: '',
        });
      } catch (error) {
        setSnackbar({
          open: true,
          message: 'Failed to send message. Please try again later.',
          severity: 'error',
        });
      }
    }
  };

  const handleCloseSnackbar = () => {
    setSnackbar({
      ...snackbar,
      open: false,
    });
  };

  return (
    <>
      {/* Hero Section */}
      <Box
        sx={{
          bgcolor: 'primary.main',
          color: 'white',
          py: 8,
          textAlign: 'center',
        }}
      >
        <Container maxWidth="md">
          <Typography variant="h2" component="h1" gutterBottom>
            Contact Us
          </Typography>
          <Typography variant="h6">
            Have questions or want to get involved? We'd love to hear from you.
          </Typography>
        </Container>
      </Box>

      {/* Contact Information */}
      <Container maxWidth="lg" sx={{ py: 8 }}>
        <Grid container spacing={4}>
          <Grid item xs={12} md={4}>
            <Card sx={{ height: '100%' }}>
              <CardContent sx={{ textAlign: 'center', py: 4 }}>
                <EmailIcon color="primary" sx={{ fontSize: 48, mb: 2 }} />
                <Typography variant="h6" gutterBottom>
                  Email Us
                </Typography>
                <Typography variant="body1" color="text.secondary">
                  <a href="mailto:info@hopebridge.org" style={{ color: 'inherit', textDecoration: 'none' }}>
                    info@hopebridge.org
                  </a>
                </Typography>
                <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                  For general inquiries
                </Typography>
                <Typography variant="body1" color="text.secondary" sx={{ mt: 2 }}>
                  <a href="mailto:donations@hopebridge.org" style={{ color: 'inherit', textDecoration: 'none' }}>
                    donations@hopebridge.org
                  </a>
                </Typography>
                <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                  For donation-related questions
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} md={4}>
            <Card sx={{ height: '100%' }}>
              <CardContent sx={{ textAlign: 'center', py: 4 }}>
                <PhoneIcon color="primary" sx={{ fontSize: 48, mb: 2 }} />
                <Typography variant="h6" gutterBottom>
                  Call Us
                </Typography>
                <Typography variant="body1" color="text.secondary">
                  <a href="tel:+441234567890" style={{ color: 'inherit', textDecoration: 'none' }}>
                    +44 123 456 7890
                  </a>
                </Typography>
                <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                  Monday to Friday, 9am - 5pm
                </Typography>
                <Box sx={{ mt: 3, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                  <AccessTimeIcon color="action" sx={{ mr: 1 }} />
                  <Typography variant="body2" color="text.secondary">
                    Response time: Within 24 hours
                  </Typography>
                </Box>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} md={4}>
            <Card sx={{ height: '100%' }}>
              <CardContent sx={{ textAlign: 'center', py: 4 }}>
                <LocationOnIcon color="primary" sx={{ fontSize: 48, mb: 2 }} />
                <Typography variant="h6" gutterBottom>
                  Visit Us
                </Typography>
                <Typography variant="body1" color="text.secondary">
                  123 Charity Lane
                </Typography>
                <Typography variant="body1" color="text.secondary">
                  London, UK
                </Typography>
                <Typography variant="body2" color="text.secondary" sx={{ mt: 2 }}>
                  By appointment only
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Container>

      {/* Contact Form */}
      <Box sx={{ bgcolor: 'background.paper', py: 8 }}>
        <Container maxWidth="md">
          <Typography variant="h3" component="h2" align="center" gutterBottom>
            Send Us a Message
          </Typography>
          <Typography variant="body1" align="center" paragraph sx={{ mb: 4 }}>
            Fill out the form below and we'll get back to you as soon as possible.
          </Typography>

          <Paper component="form" onSubmit={handleSubmit} sx={{ p: 4 }}>
            <Grid container spacing={3}>
              <Grid item xs={12} sm={6}>
                <TextField
                  required
                  fullWidth
                  id="name"
                  name="name"
                  label="Your Name"
                  value={formData.name}
                  onChange={handleInputChange}
                  error={!!errors.name}
                  helperText={errors.name}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  required
                  fullWidth
                  id="email"
                  name="email"
                  label="Email Address"
                  type="email"
                  value={formData.email}
                  onChange={handleInputChange}
                  error={!!errors.email}
                  helperText={errors.email}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="subject"
                  name="subject"
                  label="Subject"
                  value={formData.subject}
                  onChange={handleInputChange}
                  error={!!errors.subject}
                  helperText={errors.subject}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="message"
                  name="message"
                  label="Message"
                  multiline
                  rows={6}
                  value={formData.message}
                  onChange={handleInputChange}
                  error={!!errors.message}
                  helperText={errors.message}
                />
              </Grid>
              <Grid item xs={12}>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  size="large"
                  fullWidth
                >
                  Send Message
                </Button>
              </Grid>
            </Grid>
          </Paper>
        </Container>
      </Box>

      {/* FAQ Section */}
      <Container maxWidth="md" sx={{ py: 8 }}>
        <Typography variant="h3" component="h2" align="center" gutterBottom>
          Frequently Asked Questions
        </Typography>
        <Typography variant="body1" align="center" paragraph sx={{ mb: 4 }}>
          Find quick answers to common questions about HopeBridge.
        </Typography>

        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <Card sx={{ mb: 3 }}>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  How can I start a fundraising campaign?
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  To start a campaign, create an account and click on "Start a Campaign" button. 
                  Follow the step-by-step process to set up your fundraiser.
                </Typography>
              </CardContent>
            </Card>
            <Card sx={{ mb: 3 }}>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  How do I know my donation is secure?
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  We use industry-standard encryption and secure payment processors. 
                  All transactions are protected, and we never store your full credit card information.
                </Typography>
              </CardContent>
            </Card>
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  Can I donate anonymously?
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Yes, you can choose to make your donation anonymous during the checkout process.
                  Your personal information will not be displayed publicly.
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} md={6}>
            <Card sx={{ mb: 3 }}>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  How can I volunteer with HopeBridge?
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  We're always looking for volunteers! Please email volunteers@hopebridge.org 
                  with your information and areas of interest, and our team will get back to you.
                </Typography>
              </CardContent>
            </Card>
            <Card sx={{ mb: 3 }}>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  Are my donations tax-deductible?
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Yes, HopeBridge is a registered 501(c)(3) nonprofit organization, and all donations 
                  are tax-deductible to the extent allowed by law. You will receive a receipt for your records.
                </Typography>
              </CardContent>
            </Card>
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  How can I get updates on campaigns I've donated to?
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  When you make a donation, you can opt to receive updates. You can also create an account 
                  to track all your donations and receive notifications when campaigns are updated.
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Container>

      {/* Snackbar for form submission feedback */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert
          onClose={handleCloseSnackbar}
          severity={snackbar.severity}
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </>
  );
};

export default ContactPage; 