import React, { useState, useEffect } from 'react';
import { 
  Box, 
  Typography, 
  Container, 
  Grid, 
  Card, 
  CardContent, 
  Button, 
  Paper,
  List,
  ListItem,
  ListItemText,
  Divider,
  Snackbar,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  CircularProgress
} from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import { nonprofitsService } from '../services/api';

// Styled components
const HeroSection = styled(Box)(({ theme }) => ({
  backgroundColor: theme.palette.grey[100],
  padding: theme.spacing(8, 0),
  textAlign: 'center',
}));

const BenefitCard = styled(Card)(({ theme }) => ({
  height: '100%',
  display: 'flex',
  flexDirection: 'column',
  backgroundColor: theme.palette.grey[50],
  borderRadius: theme.shape.borderRadius,
  boxShadow: 'none',
}));

const StepSection = styled(Box)(({ theme }) => ({
  backgroundColor: theme.palette.grey[200],
  padding: theme.spacing(8, 0),
}));

const StepCard = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(3),
  marginBottom: theme.spacing(3),
  display: 'flex',
  alignItems: 'flex-start',
}));

const StepNumber = styled(Typography)(({ theme }) => ({
  color: theme.palette.primary.main,
  fontWeight: 'bold',
  fontSize: '2rem',
  marginRight: theme.spacing(2),
}));

const TestimonialSection = styled(Box)(({ theme }) => ({
  padding: theme.spacing(8, 0),
}));

const TestimonialCard = styled(Card)(({ theme }) => ({
  height: '100%',
  display: 'flex',
  flexDirection: 'column',
  padding: theme.spacing(3),
  backgroundColor: theme.palette.grey[50],
}));

const NonprofitsPage = () => {
  const [testimonials, setTestimonials] = useState([]);
  const [loading, setLoading] = useState(false);
  const [openSurvey, setOpenSurvey] = useState(false);
  const [surveyData, setSurveyData] = useState({
    name: '',
    email: '',
    organization: '',
    feedback: '',
    rating: 5,
    improvements: ''
  });
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success'
  });

  useEffect(() => {
    const fetchTestimonials = async () => {
      setLoading(true);
      try {
        const response = await nonprofitsService.getNonprofitTestimonials();
        setTestimonials(response.data);
      } catch (error) {
        console.error('Error fetching testimonials:', error);
        // Use default testimonials if API fails
        setTestimonials([
          {
            id: 1,
            content: "HopeBridge has been instrumental in helping us reach donors worldwide and scale our impact.",
            author_name: "Education Nonprofit",
            country: "Kenya"
          },
          {
            id: 2,
            content: "The training and support we received helped us improve our fundraising strategy significantly.",
            author_name: "Healthcare Initiative",
            country: "India"
          },
          {
            id: 3,
            content: "Being part of HopeBridge opened doors to corporate partnerships we couldn't access before.",
            author_name: "Environmental Organization",
            country: "Brazil"
          }
        ]);
      } finally {
        setLoading(false);
      }
    };

    fetchTestimonials();
  }, []);

  const handleOpenSurvey = () => {
    setOpenSurvey(true);
  };

  const handleCloseSurvey = () => {
    setOpenSurvey(false);
  };

  const handleSurveyChange = (e) => {
    const { name, value } = e.target;
    setSurveyData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmitSurvey = async () => {
    try {
      await nonprofitsService.submitSurvey(surveyData);
      setSnackbar({
        open: true,
        message: 'Thank you for completing our survey!',
        severity: 'success'
      });
      handleCloseSurvey();
    } catch (error) {
      console.error('Error submitting survey:', error);
      setSnackbar({
        open: true,
        message: 'Failed to submit survey. Please try again.',
        severity: 'error'
      });
    }
  };

  const handleCloseSnackbar = () => {
    setSnackbar(prev => ({
      ...prev,
      open: false
    }));
  };

  return (
    <>
      {/* Hero Section */}
      <HeroSection>
        <Container>
          <Typography variant="h3" component="h1" gutterBottom>
            For Nonprofits
          </Typography>
          <Typography variant="h6" color="textSecondary" paragraph>
            Join our global community and accelerate your impact
          </Typography>
          <Button 
            variant="contained" 
            color="primary" 
            size="large" 
            component={RouterLink} 
            to="/application-form"
            sx={{ mt: 2 }}
          >
            Apply Now
          </Button>
        </Container>
      </HeroSection>

      {/* Benefits Section */}
      <Container sx={{ py: 8 }}>
        <Typography variant="h4" component="h2" gutterBottom sx={{ mb: 4 }}>
          Benefits of Joining HopeBridge
        </Typography>
        <Grid container spacing={4}>
          <Grid item xs={12} md={4}>
            <BenefitCard>
              <CardContent sx={{ textAlign: 'center', flexGrow: 1 }}>
                <Typography variant="h6" color="primary" gutterBottom>
                  Access to Funding
                </Typography>
                <Typography variant="body1">
                  Connect with individual and corporate donors worldwide
                </Typography>
              </CardContent>
            </BenefitCard>
          </Grid>
          <Grid item xs={12} md={4}>
            <BenefitCard>
              <CardContent sx={{ textAlign: 'center', flexGrow: 1 }}>
                <Typography variant="h6" color="primary" gutterBottom>
                  Capacity Building
                </Typography>
                <Typography variant="body1">
                  Access training and resources to strengthen your organization
                </Typography>
              </CardContent>
            </BenefitCard>
          </Grid>
          <Grid item xs={12} md={4}>
            <BenefitCard>
              <CardContent sx={{ textAlign: 'center', flexGrow: 1 }}>
                <Typography variant="h6" color="primary" gutterBottom>
                  Global Network
                </Typography>
                <Typography variant="body1">
                  Join a community of 31,000+ nonprofits worldwide
                </Typography>
              </CardContent>
            </BenefitCard>
          </Grid>
        </Grid>
      </Container>

      {/* How to Join Section */}
      <StepSection>
        <Container>
          <Typography variant="h4" component="h2" gutterBottom sx={{ mb: 4 }}>
            How to Join
          </Typography>
          
          <StepCard>
            <StepNumber>1</StepNumber>
            <Box>
              <Typography variant="h6" gutterBottom>
                Submit Application
              </Typography>
              <Typography variant="body1">
                Complete our online application form with information about your organization
              </Typography>
            </Box>
          </StepCard>
          
          <StepCard>
            <StepNumber>2</StepNumber>
            <Box>
              <Typography variant="h6" gutterBottom>
                Due Diligence Review
              </Typography>
              <Typography variant="body1">
                Our team reviews your application and verifies your organization
              </Typography>
            </Box>
          </StepCard>
          
          <StepCard>
            <StepNumber>3</StepNumber>
            <Box>
              <Typography variant="h6" gutterBottom>
                Launch Your Project
              </Typography>
              <Typography variant="body1">
                Create your first project and start receiving donations
              </Typography>
            </Box>
          </StepCard>
        </Container>
      </StepSection>

      {/* Testimonials Section */}
      <TestimonialSection>
        <Container>
          <Typography variant="h4" component="h2" gutterBottom sx={{ mb: 4, textAlign: 'center' }}>
            What Our Partners Say
          </Typography>
          {loading ? (
            <Box display="flex" justifyContent="center" my={4}>
              <CircularProgress />
            </Box>
          ) : (
            <Grid container spacing={4}>
              {testimonials.length > 0 ? (
                testimonials.slice(0, 3).map((testimonial) => (
                  <Grid item xs={12} md={4} key={testimonial.id}>
                    <TestimonialCard>
                      <Typography variant="body1" paragraph sx={{ fontStyle: 'italic' }}>
                        "{testimonial.content}"
                      </Typography>
                      <Typography variant="subtitle2" color="textSecondary">
                        - {testimonial.author_name}, {testimonial.country}
                      </Typography>
                    </TestimonialCard>
                  </Grid>
                ))
              ) : (
                <>
                  <Grid item xs={12} md={4}>
                    <TestimonialCard>
                      <Typography variant="body1" paragraph sx={{ fontStyle: 'italic' }}>
                        "HopeBridge has been instrumental in helping us reach donors worldwide and scale our impact."
                      </Typography>
                      <Typography variant="subtitle2" color="textSecondary">
                        - Education Nonprofit, Kenya
                      </Typography>
                    </TestimonialCard>
                  </Grid>
                  <Grid item xs={12} md={4}>
                    <TestimonialCard>
                      <Typography variant="body1" paragraph sx={{ fontStyle: 'italic' }}>
                        "The training and support we received helped us improve our fundraising strategy significantly."
                      </Typography>
                      <Typography variant="subtitle2" color="textSecondary">
                        - Healthcare Initiative, India
                      </Typography>
                    </TestimonialCard>
                  </Grid>
                  <Grid item xs={12} md={4}>
                    <TestimonialCard>
                      <Typography variant="body1" paragraph sx={{ fontStyle: 'italic' }}>
                        "Being part of HopeBridge opened doors to corporate partnerships we couldn't access before."
                      </Typography>
                      <Typography variant="subtitle2" color="textSecondary">
                        - Environmental Organization, Brazil
                      </Typography>
                    </TestimonialCard>
                  </Grid>
                </>
              )}
            </Grid>
          )}
          <Box sx={{ textAlign: 'center', mt: 4 }}>
            <Button 
              variant="contained" 
              color="primary"
              onClick={handleOpenSurvey}
            >
              Complete Our Survey
            </Button>
          </Box>
        </Container>
      </TestimonialSection>

      {/* Survey Dialog */}
      <Dialog open={openSurvey} onClose={handleCloseSurvey} maxWidth="sm" fullWidth>
        <DialogTitle>Share Your Feedback</DialogTitle>
        <DialogContent>
          <Grid container spacing={2} sx={{ mt: 1 }}>
            <Grid item xs={12} sm={6}>
              <TextField
                name="name"
                label="Your Name"
                value={surveyData.name}
                onChange={handleSurveyChange}
                fullWidth
                required
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                name="email"
                label="Email Address"
                type="email"
                value={surveyData.email}
                onChange={handleSurveyChange}
                fullWidth
                required
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                name="organization"
                label="Organization Name"
                value={surveyData.organization}
                onChange={handleSurveyChange}
                fullWidth
                required
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                name="feedback"
                label="Your Feedback"
                value={surveyData.feedback}
                onChange={handleSurveyChange}
                multiline
                rows={4}
                fullWidth
                required
              />
            </Grid>
            <Grid item xs={12}>
              <FormControl fullWidth>
                <InputLabel>Rating</InputLabel>
                <Select
                  name="rating"
                  value={surveyData.rating}
                  onChange={handleSurveyChange}
                  label="Rating"
                >
                  {[1, 2, 3, 4, 5].map((rating) => (
                    <MenuItem key={rating} value={rating}>
                      {rating} {rating === 1 ? 'Star' : 'Stars'}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12}>
              <TextField
                name="improvements"
                label="Suggested Improvements"
                value={surveyData.improvements}
                onChange={handleSurveyChange}
                multiline
                rows={3}
                fullWidth
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseSurvey}>Cancel</Button>
          <Button 
            onClick={handleSubmitSurvey} 
            variant="contained" 
            color="primary"
            disabled={!surveyData.name || !surveyData.email || !surveyData.organization || !surveyData.feedback}
          >
            Submit
          </Button>
        </DialogActions>
      </Dialog>

      {/* Snackbar for notifications */}
      <Snackbar 
        open={snackbar.open} 
        autoHideDuration={6000} 
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={handleCloseSnackbar} severity={snackbar.severity} sx={{ width: '100%' }}>
          {snackbar.message}
        </Alert>
      </Snackbar>
    </>
  );
};

export default NonprofitsPage; 