import React, { useState, useEffect } from 'react';
import { useParams, Link as RouterLink } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Grid,
  Button,
  Card,
  CardContent,
  Divider,
  Avatar,
  Chip,
  LinearProgress,
  Tabs,
  Tab,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  TextField,
  Paper,
  IconButton,
  Breadcrumbs,
  Link,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  FormControl,
  FormControlLabel,
  RadioGroup,
  Radio,
  InputAdornment,
} from '@mui/material';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import ShareIcon from '@mui/icons-material/Share';
import FavoriteIcon from '@mui/icons-material/Favorite';
import PersonIcon from '@mui/icons-material/Person';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';

// Import axios conditionally
let axios;
if (process.env.NODE_ENV === 'test') {
  axios = require('../tests/mocks/axios').default;
} else {
  axios = require('axios').default;
}

// Mock campaign data
const mockCampaign = {
  id: 1,
  title: 'Clean Water Initiative',
  description: 'Providing clean water to communities in need across Africa.',
  fullDescription: `
    <p>Access to clean water is a fundamental human right, yet millions of people across Africa still lack this basic necessity. Our Clean Water Initiative aims to change that by implementing sustainable water solutions in communities that need them most.</p>
    
    <p>With your support, we will:</p>
    <ul>
      <li>Drill wells in 15 villages across Kenya, providing clean water to over 10,000 people</li>
      <li>Install water filtration systems in 20 schools, ensuring children have safe drinking water</li>
      <li>Train local technicians to maintain and repair water infrastructure</li>
      <li>Educate communities about water conservation and hygiene practices</li>
    </ul>
    
    <p>The impact of clean water extends far beyond health. When communities gain access to clean water:</p>
    <ul>
      <li>Children (especially girls) can attend school instead of walking hours to collect water</li>
      <li>Waterborne diseases decrease dramatically, reducing healthcare costs</li>
      <li>Agricultural productivity increases, improving food security</li>
      <li>Women have more time for education and income-generating activities</li>
    </ul>
    
    <p>Your donation will directly fund the equipment, training, and implementation needed to bring clean water to these communities. Together, we can make a lasting difference in thousands of lives.</p>
  `,
  image: '/images/campaigns/water.jpg',
  gallery: [
    '/images/campaigns/water-1.jpg',
    '/images/campaigns/water-2.jpg',
    '/images/campaigns/water-3.jpg',
  ],
  raised: 12500,
  goal: 25000,
  daysLeft: 15,
  location: 'Kenya',
  category: 'Health & Wellness',
  organizer: {
    name: 'Water for All Foundation',
    image: '/images/organizers/water-foundation.jpg',
    description: 'A non-profit organization dedicated to providing clean water solutions worldwide.',
    campaignsCount: 8,
    verified: true,
  },
  updates: [
    {
      id: 1,
      date: '2023-05-15',
      title: 'First well completed!',
      content: 'We are excited to announce that we have completed drilling the first well in Makueni County. The community now has access to clean water for the first time in decades.',
    },
    {
      id: 2,
      date: '2023-04-02',
      title: 'Project kickoff',
      content: 'We have officially begun our Clean Water Initiative. Our team has arrived in Kenya and has started the initial assessments of the target communities.',
    },
  ],
  donors: [
    { id: 1, name: 'John Smith', amount: 500, date: '2023-05-20', message: 'Keep up the great work!' },
    { id: 2, name: 'Emily Johnson', amount: 250, date: '2023-05-18', message: 'Happy to support this important cause.' },
    { id: 3, name: 'Michael Brown', amount: 1000, date: '2023-05-15', message: 'Clean water is a basic human right. Proud to contribute.' },
    { id: 4, name: 'Sarah Williams', amount: 100, date: '2023-05-10', message: null },
    { id: 5, name: 'Anonymous', amount: 750, date: '2023-05-05', message: 'Wishing you success in this vital mission.' },
  ],
  comments: [
    {
      id: 1,
      name: 'David Wilson',
      date: '2023-05-18',
      content: 'This is such an important initiative. I visited Kenya last year and saw firsthand the need for clean water in rural communities.',
      avatar: '/images/avatars/david.jpg',
    },
    {
      id: 2,
      name: 'Lisa Chen',
      date: '2023-05-15',
      content: 'I love how transparent this organization is about where the funds go. Keep up the great work!',
      avatar: '/images/avatars/lisa.jpg',
    },
  ],
};

const CampaignDetailPage = () => {
  const { id } = useParams();
  const [campaign, setCampaign] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error] = useState(null);
  const [tabValue, setTabValue] = useState(0);
  const [comment, setComment] = useState('');
  const [openDonateDialog, setOpenDonateDialog] = useState(false);
  const [donationAmount, setDonationAmount] = useState('25');
  const [customAmount, setCustomAmount] = useState('');

  // Fetch campaign data
  useEffect(() => {
    // In a real app, this would be an API call
    // const fetchCampaign = async () => {
    //   try {
    //     const response = await axios.get(`/api/campaigns/${id}`);
    //     setCampaign(response.data);
    //   } catch (error) {
    //     setError('Failed to load campaign details. Please try again later.');
    //     console.error('Error fetching campaign:', error);
    //   } finally {
    //     setLoading(false);
    //   }
    // };
    // fetchCampaign();

    // Using mock data
    setTimeout(() => {
      setCampaign(mockCampaign);
      setLoading(false);
    }, 500);
  }, [id]);

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  const handleCommentChange = (event) => {
    setComment(event.target.value);
  };

  const handleSubmitComment = (event) => {
    event.preventDefault();
    // In a real app, this would send the comment to the API
    // For now, just clear the input
    setComment('');
    alert('Comment submitted! (This would be saved in a real app)');
  };

  const handleOpenDonateDialog = () => {
    setOpenDonateDialog(true);
  };

  const handleCloseDonateDialog = () => {
    setOpenDonateDialog(false);
  };

  const handleDonationAmountChange = (event) => {
    setDonationAmount(event.target.value);
    if (event.target.value === 'custom') {
      setCustomAmount('');
    }
  };

  const handleCustomAmountChange = (event) => {
    setCustomAmount(event.target.value);
  };

  const handleDonateSubmit = () => {
    // In a real app, this would process the donation through Stripe/PayPal
    console.log('Processing donation:', donationAmount === 'custom' ? customAmount : donationAmount);
    handleCloseDonateDialog();
    alert('Thank you for your donation! (This would process payment in a real app)');
  };

  if (loading) {
    return (
      <Container maxWidth="lg" sx={{ py: 8, textAlign: 'center' }}>
        <Typography variant="h5">Loading campaign details...</Typography>
        <LinearProgress sx={{ mt: 4, mx: 'auto', maxWidth: 500 }} />
      </Container>
    );
  }

  if (error) {
    return (
      <Container maxWidth="lg" sx={{ py: 8, textAlign: 'center' }}>
        <Typography variant="h5" color="error" gutterBottom>
          {error}
        </Typography>
        <Button component={RouterLink} to="/campaigns" variant="contained">
          Back to Campaigns
        </Button>
      </Container>
    );
  }

  if (!campaign) {
    return (
      <Container maxWidth="lg" sx={{ py: 8, textAlign: 'center' }}>
        <Typography variant="h5" gutterBottom>
          Campaign not found
        </Typography>
        <Button component={RouterLink} to="/campaigns" variant="contained">
          Back to Campaigns
        </Button>
      </Container>
    );
  }

  const progressPercentage = (campaign.raised / campaign.goal) * 100;

  return (
    <>
      {/* Breadcrumbs */}
      <Box sx={{ bgcolor: 'background.paper', py: 2 }}>
        <Container maxWidth="lg">
          <Breadcrumbs separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
            <Link component={RouterLink} to="/" color="inherit">
              Home
            </Link>
            <Link component={RouterLink} to="/campaigns" color="inherit">
              Campaigns
            </Link>
            <Typography color="text.primary">{campaign.title}</Typography>
          </Breadcrumbs>
        </Container>
      </Box>

      {/* Campaign Header */}
      <Box sx={{ bgcolor: 'background.paper', py: 4 }}>
        <Container maxWidth="lg">
          <Grid container spacing={4}>
            {/* Campaign Image */}
            <Grid item xs={12} md={8}>
              <Box
                component="img"
                src={campaign.image || 'https://source.unsplash.com/random?charity'}
                alt={campaign.title}
                sx={{
                  width: '100%',
                  height: 400,
                  objectFit: 'cover',
                  borderRadius: 2,
                }}
              />
            </Grid>

            {/* Campaign Info */}
            <Grid item xs={12} md={4}>
              <Card sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
                <CardContent sx={{ flexGrow: 1 }}>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                    <Chip label={campaign.category} color="primary" />
                    <IconButton aria-label="share">
                      <ShareIcon />
                    </IconButton>
                  </Box>

                  <Typography variant="h4" component="h1" gutterBottom>
                    {campaign.title}
                  </Typography>

                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                    <LocationOnIcon sx={{ fontSize: 20, mr: 0.5, color: 'text.secondary' }} />
                    <Typography variant="body2" color="text.secondary">
                      {campaign.location}
                    </Typography>
                  </Box>

                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
                    <AccessTimeIcon sx={{ fontSize: 20, mr: 0.5, color: 'text.secondary' }} />
                    <Typography variant="body2" color="text.secondary">
                      {campaign.daysLeft} days left
                    </Typography>
                  </Box>

                  <Box sx={{ mb: 3 }}>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                      <Typography variant="h5" color="primary.main">
                        ${campaign.raised.toLocaleString()}
                      </Typography>
                      <Typography variant="body1" color="text.secondary">
                        of ${campaign.goal.toLocaleString()} goal
                      </Typography>
                    </Box>
                    <LinearProgress
                      variant="determinate"
                      value={progressPercentage > 100 ? 100 : progressPercentage}
                      sx={{ height: 10, borderRadius: 5, mb: 1 }}
                    />
                    <Typography variant="body2" color="text.secondary">
                      {Math.round(progressPercentage)}% funded by {campaign.donors.length} donors
                    </Typography>
                  </Box>

                  <Button
                    variant="contained"
                    color="primary"
                    size="large"
                    fullWidth
                    onClick={handleOpenDonateDialog}
                    sx={{ mb: 2 }}
                  >
                    Donate Now
                  </Button>

                  <Button
                    variant="outlined"
                    color="primary"
                    fullWidth
                    startIcon={<FavoriteIcon />}
                  >
                    Save Campaign
                  </Button>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Container>
      </Box>

      {/* Campaign Content */}
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Grid container spacing={4}>
          {/* Main Content */}
          <Grid item xs={12} md={8}>
            <Box sx={{ mb: 4 }}>
              <Tabs value={tabValue} onChange={handleTabChange} aria-label="campaign tabs">
                <Tab label="About" id="tab-0" aria-controls="tabpanel-0" />
                <Tab label="Updates" id="tab-1" aria-controls="tabpanel-1" />
                <Tab label="Comments" id="tab-2" aria-controls="tabpanel-2" />
                <Tab label="Donors" id="tab-3" aria-controls="tabpanel-3" />
              </Tabs>
              <Divider />
            </Box>

            {/* About Tab */}
            <Box
              role="tabpanel"
              hidden={tabValue !== 0}
              id="tabpanel-0"
              aria-labelledby="tab-0"
            >
              {tabValue === 0 && (
                <Box>
                  <Typography variant="body1" paragraph>
                    {campaign.description}
                  </Typography>
                  <div dangerouslySetInnerHTML={{ __html: campaign.fullDescription }} />
                </Box>
              )}
            </Box>

            {/* Updates Tab */}
            <Box
              role="tabpanel"
              hidden={tabValue !== 1}
              id="tabpanel-1"
              aria-labelledby="tab-1"
            >
              {tabValue === 1 && (
                <Box>
                  {campaign.updates.length === 0 ? (
                    <Typography variant="body1">No updates yet. Check back soon!</Typography>
                  ) : (
                    campaign.updates.map((update) => (
                      <Paper key={update.id} sx={{ p: 3, mb: 3 }}>
                        <Typography variant="h6" gutterBottom>
                          {update.title}
                        </Typography>
                        <Typography variant="caption" color="text.secondary" display="block" gutterBottom>
                          {new Date(update.date).toLocaleDateString('en-US', {
                            year: 'numeric',
                            month: 'long',
                            day: 'numeric',
                          })}
                        </Typography>
                        <Divider sx={{ my: 2 }} />
                        <Typography variant="body1">{update.content}</Typography>
                      </Paper>
                    ))
                  )}
                </Box>
              )}
            </Box>

            {/* Comments Tab */}
            <Box
              role="tabpanel"
              hidden={tabValue !== 2}
              id="tabpanel-2"
              aria-labelledby="tab-2"
            >
              {tabValue === 2 && (
                <Box>
                  <Paper component="form" onSubmit={handleSubmitComment} sx={{ p: 3, mb: 4 }}>
                    <Typography variant="h6" gutterBottom>
                      Leave a Comment
                    </Typography>
                    <TextField
                      fullWidth
                      multiline
                      rows={4}
                      placeholder="Share your thoughts about this campaign..."
                      value={comment}
                      onChange={handleCommentChange}
                      sx={{ mb: 2 }}
                    />
                    <Button
                      type="submit"
                      variant="contained"
                      color="primary"
                      disabled={!comment.trim()}
                    >
                      Post Comment
                    </Button>
                  </Paper>

                  {campaign.comments.length === 0 ? (
                    <Typography variant="body1">No comments yet. Be the first to comment!</Typography>
                  ) : (
                    campaign.comments.map((comment) => (
                      <Paper key={comment.id} sx={{ p: 3, mb: 3 }}>
                        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                          <Avatar
                            src={comment.avatar}
                            alt={comment.name}
                            sx={{ mr: 2 }}
                          />
                          <Box>
                            <Typography variant="subtitle1">{comment.name}</Typography>
                            <Typography variant="caption" color="text.secondary">
                              {new Date(comment.date).toLocaleDateString('en-US', {
                                year: 'numeric',
                                month: 'long',
                                day: 'numeric',
                              })}
                            </Typography>
                          </Box>
                        </Box>
                        <Typography variant="body1">{comment.content}</Typography>
                      </Paper>
                    ))
                  )}
                </Box>
              )}
            </Box>

            {/* Donors Tab */}
            <Box
              role="tabpanel"
              hidden={tabValue !== 3}
              id="tabpanel-3"
              aria-labelledby="tab-3"
            >
              {tabValue === 3 && (
                <Box>
                  <List>
                    {campaign.donors.map((donor) => (
                      <React.Fragment key={donor.id}>
                        <ListItem alignItems="flex-start">
                          <ListItemAvatar>
                            <Avatar>
                              <PersonIcon />
                            </Avatar>
                          </ListItemAvatar>
                          <ListItemText
                            primary={
                              <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                                <Typography variant="subtitle1">{donor.name}</Typography>
                                <Typography variant="subtitle1" color="primary.main">
                                  ${donor.amount}
                                </Typography>
                              </Box>
                            }
                            secondary={
                              <>
                                <Typography
                                  component="span"
                                  variant="body2"
                                  color="text.secondary"
                                  sx={{ display: 'block' }}
                                >
                                  {new Date(donor.date).toLocaleDateString('en-US', {
                                    year: 'numeric',
                                    month: 'long',
                                    day: 'numeric',
                                  })}
                                </Typography>
                                {donor.message && (
                                  <Typography
                                    component="span"
                                    variant="body2"
                                    sx={{ display: 'block', mt: 1 }}
                                  >
                                    "{donor.message}"
                                  </Typography>
                                )}
                              </>
                            }
                          />
                        </ListItem>
                        <Divider variant="inset" component="li" />
                      </React.Fragment>
                    ))}
                  </List>
                </Box>
              )}
            </Box>
          </Grid>

          {/* Sidebar */}
          <Grid item xs={12} md={4}>
            {/* Organizer Info */}
            <Card sx={{ mb: 4 }}>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  Campaign Organizer
                </Typography>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                  <Avatar
                    src={campaign.organizer.image}
                    alt={campaign.organizer.name}
                    sx={{ width: 60, height: 60, mr: 2 }}
                  />
                  <Box>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <Typography variant="subtitle1">{campaign.organizer.name}</Typography>
                      {campaign.organizer.verified && (
                        <Chip
                          label="Verified"
                          color="primary"
                          size="small"
                          sx={{ ml: 1 }}
                        />
                      )}
                    </Box>
                    <Typography variant="body2" color="text.secondary">
                      {campaign.organizer.campaignsCount} campaigns
                    </Typography>
                  </Box>
                </Box>
                <Typography variant="body2" paragraph>
                  {campaign.organizer.description}
                </Typography>
                <Button variant="outlined" fullWidth>
                  Contact Organizer
                </Button>
              </CardContent>
            </Card>

            {/* Similar Campaigns */}
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  Similar Campaigns
                </Typography>
                <Typography variant="body2" color="text.secondary" paragraph>
                  Explore other campaigns in {campaign.category}
                </Typography>
                <Button
                  component={RouterLink}
                  to="/campaigns"
                  variant="outlined"
                  fullWidth
                >
                  View More Campaigns
                </Button>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Container>

      {/* Donation Dialog */}
      <Dialog
        open={openDonateDialog}
        onClose={handleCloseDonateDialog}
        aria-labelledby="donation-dialog-title"
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle id="donation-dialog-title">Donate to {campaign.title}</DialogTitle>
        <DialogContent>
          <DialogContentText sx={{ mb: 3 }}>
            Your donation will help {campaign.organizer.name} reach their goal of ${campaign.goal.toLocaleString()}.
          </DialogContentText>
          <FormControl component="fieldset" sx={{ width: '100%', mb: 3 }}>
            <RadioGroup
              aria-label="donation-amount"
              name="donation-amount"
              value={donationAmount}
              onChange={handleDonationAmountChange}
            >
              <Grid container spacing={2}>
                <Grid item xs={6} sm={3}>
                  <FormControlLabel
                    value="10"
                    control={<Radio />}
                    label="$10"
                    sx={{ width: '100%' }}
                  />
                </Grid>
                <Grid item xs={6} sm={3}>
                  <FormControlLabel
                    value="25"
                    control={<Radio />}
                    label="$25"
                    sx={{ width: '100%' }}
                  />
                </Grid>
                <Grid item xs={6} sm={3}>
                  <FormControlLabel
                    value="50"
                    control={<Radio />}
                    label="$50"
                    sx={{ width: '100%' }}
                  />
                </Grid>
                <Grid item xs={6} sm={3}>
                  <FormControlLabel
                    value="100"
                    control={<Radio />}
                    label="$100"
                    sx={{ width: '100%' }}
                  />
                </Grid>
                <Grid item xs={12}>
                  <FormControlLabel
                    value="custom"
                    control={<Radio />}
                    label="Custom Amount"
                    sx={{ width: '100%' }}
                  />
                </Grid>
              </Grid>
            </RadioGroup>
          </FormControl>

          {donationAmount === 'custom' && (
            <TextField
              autoFocus
              margin="dense"
              id="custom-amount"
              label="Enter amount"
              type="number"
              fullWidth
              variant="outlined"
              value={customAmount}
              onChange={handleCustomAmountChange}
              InputProps={{
                startAdornment: <InputAdornment position="start">$</InputAdornment>,
              }}
            />
          )}

          <TextField
            margin="dense"
            id="name"
            label="Your Name"
            type="text"
            fullWidth
            variant="outlined"
            sx={{ mt: 3 }}
          />
          <TextField
            margin="dense"
            id="email"
            label="Email Address"
            type="email"
            fullWidth
            variant="outlined"
          />
          <TextField
            margin="dense"
            id="message"
            label="Leave a message (optional)"
            multiline
            rows={2}
            fullWidth
            variant="outlined"
          />
        </DialogContent>
        <DialogActions sx={{ px: 3, pb: 3 }}>
          <Button onClick={handleCloseDonateDialog}>Cancel</Button>
          <Button
            onClick={handleDonateSubmit}
            variant="contained"
            color="primary"
            disabled={donationAmount === 'custom' && !customAmount}
          >
            Donate ${donationAmount === 'custom' ? customAmount : donationAmount}
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default CampaignDetailPage; 