import React, { useState, useEffect, useRef } from 'react';
import {
  Box,
  Button,
  Card,
  CardContent,
  Fab,
  IconButton,
  InputAdornment,
  Paper,
  TextField,
  Typography,
  Zoom,
  Avatar,
} from '@mui/material';
import ChatIcon from '@mui/icons-material/Chat';
import CloseIcon from '@mui/icons-material/Close';
import SendIcon from '@mui/icons-material/Send';

// Import axios from our mock in test environment or real axios in production
let axios;
if (process.env.NODE_ENV === 'test') {
  axios = require('../../tests/mocks/axios').default;
} else {
  axios = require('axios').default;
}

// Mock responses for the chatbot
const botResponses = {
  greeting: [
    "Hello! How can I help you today?",
    "Hi there! I'm HopeBridge's virtual assistant. What can I do for you?",
    "Welcome to HopeBridge! How may I assist you?",
  ],
  donation: [
    "You can donate by clicking the 'Donate Now' button at the top of the page. We accept various payment methods including credit cards and PayPal.",
    "We appreciate your interest in donating! You can make a donation through our secure payment system by clicking 'Donate Now'.",
  ],
  campaigns: [
    "We have several active campaigns. You can view all of them on our Campaigns page. Is there a specific cause you're interested in?",
    "Our current campaigns focus on clean water, education, and emergency relief. You can find more details on our Campaigns page.",
  ],
  volunteer: [
    "We're always looking for volunteers! Please visit our 'Get Involved' page or contact us directly at volunteers@hopebridge.org.",
    "Thank you for your interest in volunteering! You can sign up on our website or email us at volunteers@hopebridge.org for more information.",
  ],
  contact: [
    "You can reach us at info@hopebridge.org or call us at +44 123 456 7890. Our office hours are Monday to Friday, 9 AM to 5 PM.",
    "Feel free to contact us through our Contact page, by email at info@hopebridge.org, or by phone at +44 123 456 7890.",
  ],
  default: [
    "I'm not sure I understand. Could you please rephrase your question?",
    "I'm still learning! Could you try asking in a different way?",
    "I don't have that information right now. Would you like to speak with a human representative?",
  ],
};

// Function to get a random response from an array
const getRandomResponse = (responses) => {
  const randomIndex = Math.floor(Math.random() * responses.length);
  return responses[randomIndex];
};

// Function to determine which response category to use
const getBotResponse = (message) => {
  const lowerMessage = message.toLowerCase();
  
  if (lowerMessage.includes('hello') || lowerMessage.includes('hi') || lowerMessage.includes('hey')) {
    return getRandomResponse(botResponses.greeting);
  } else if (lowerMessage.includes('donate') || lowerMessage.includes('donation') || lowerMessage.includes('money') || lowerMessage.includes('give')) {
    return getRandomResponse(botResponses.donation);
  } else if (lowerMessage.includes('campaign') || lowerMessage.includes('cause') || lowerMessage.includes('project')) {
    return getRandomResponse(botResponses.campaigns);
  } else if (lowerMessage.includes('volunteer') || lowerMessage.includes('help out') || lowerMessage.includes('assist')) {
    return getRandomResponse(botResponses.volunteer);
  } else if (lowerMessage.includes('contact') || lowerMessage.includes('email') || lowerMessage.includes('phone') || lowerMessage.includes('call')) {
    return getRandomResponse(botResponses.contact);
  } else {
    return getRandomResponse(botResponses.default);
  }
};

const ChatBot = () => {
  const [open, setOpen] = useState(false);
  const [messages, setMessages] = useState([
    { id: 1, text: "Hello! How can I help you today?", sender: 'bot', timestamp: new Date() },
  ]);
  const [newMessage, setNewMessage] = useState('');
  const [typing, setTyping] = useState(false);
  const messagesEndRef = useRef(null);

  const toggleChat = () => {
    setOpen(!open);
  };

  const handleSendMessage = async (e) => {
    e.preventDefault();
    
    if (!newMessage.trim()) return;
    
    // Add user message
    const userMessage = {
      id: messages.length + 1,
      text: newMessage,
      sender: 'user',
      timestamp: new Date(),
    };
    
    setMessages((prev) => [...prev, userMessage]);
    setNewMessage('');
    setTyping(true);
    
    // In a real app, this would be an API call to a backend service
    // Simulate API delay
    setTimeout(() => {
      const botMessage = {
        id: messages.length + 2,
        text: getBotResponse(newMessage),
        sender: 'bot',
        timestamp: new Date(),
      };
      
      setMessages((prev) => [...prev, botMessage]);
      setTyping(false);
    }, 1000);
  };

  // Scroll to bottom when messages change
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  return (
    <>
      <Zoom in={!open}>
        <Fab
          color="primary"
          aria-label="chat"
          onClick={toggleChat}
          sx={{
            position: 'fixed',
            bottom: 24,
            right: 24,
            zIndex: 1000,
          }}
        >
          <ChatIcon />
        </Fab>
      </Zoom>

      <Card
        sx={{
          position: 'fixed',
          bottom: open ? 24 : -500, // Slide up/down
          right: 24,
          width: 350,
          maxWidth: '90vw',
          height: 450,
          maxHeight: '70vh',
          zIndex: 1000,
          display: 'flex',
          flexDirection: 'column',
          transition: 'bottom 0.3s ease-in-out',
          boxShadow: 4,
          borderRadius: 2,
          overflow: 'hidden',
        }}
      >
        {/* Chat Header */}
        <Box
          sx={{
            p: 2,
            bgcolor: 'primary.main',
            color: 'white',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
          }}
        >
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Avatar
              src="/images/logo-small.png"
              alt="HopeBridge"
              sx={{ width: 32, height: 32, mr: 1 }}
            />
            <Typography variant="h6">HopeBridge Chat</Typography>
          </Box>
          <IconButton color="inherit" onClick={toggleChat} edge="end" aria-label="close">
            <CloseIcon />
          </IconButton>
        </Box>

        {/* Chat Messages */}
        <Box
          sx={{
            p: 2,
            flexGrow: 1,
            overflow: 'auto',
            bgcolor: '#f5f5f5',
          }}
        >
          {messages.map((message) => (
            <Box
              key={message.id}
              sx={{
                display: 'flex',
                justifyContent: message.sender === 'user' ? 'flex-end' : 'flex-start',
                mb: 2,
              }}
            >
              <Paper
                sx={{
                  p: 1.5,
                  maxWidth: '80%',
                  borderRadius: 2,
                  bgcolor: message.sender === 'user' ? 'primary.main' : 'white',
                  color: message.sender === 'user' ? 'white' : 'text.primary',
                }}
              >
                <Typography variant="body2">{message.text}</Typography>
                <Typography
                  variant="caption"
                  sx={{
                    display: 'block',
                    textAlign: message.sender === 'user' ? 'right' : 'left',
                    mt: 0.5,
                    opacity: 0.7,
                  }}
                >
                  {message.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                </Typography>
              </Paper>
            </Box>
          ))}
          {typing && (
            <Box
              sx={{
                display: 'flex',
                justifyContent: 'flex-start',
                mb: 2,
              }}
            >
              <Paper
                sx={{
                  p: 1.5,
                  borderRadius: 2,
                  bgcolor: 'white',
                }}
              >
                <Typography variant="body2">
                  <span className="typing-indicator">
                    <span>.</span>
                    <span>.</span>
                    <span>.</span>
                  </span>
                </Typography>
              </Paper>
            </Box>
          )}
          <div ref={messagesEndRef} />
        </Box>

        {/* Chat Input */}
        <Box
          component="form"
          onSubmit={handleSendMessage}
          sx={{
            p: 2,
            bgcolor: 'background.paper',
            borderTop: 1,
            borderColor: 'divider',
          }}
        >
          <TextField
            fullWidth
            placeholder="Type your message..."
            value={newMessage}
            onChange={(e) => setNewMessage(e.target.value)}
            variant="outlined"
            size="small"
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    color="primary"
                    edge="end"
                    onClick={handleSendMessage}
                    disabled={!newMessage.trim()}
                    aria-label="send"
                  >
                    <SendIcon />
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
        </Box>
      </Card>
    </>
  );
};

export default ChatBot; 