import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  Avatar,
  Divider,
  Button,
  Paper,
  CircularProgress,
  Alert,
} from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';
import axios from 'axios';

const AboutPage = () => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [pageData, setPageData] = useState({
    team_members: [],
    testimonials: [],
    partners: [],
    impact_stats: [],
    mission: '',
    vision: '',
    values: '',
    story: '',
  });

  useEffect(() => {
    const fetchAboutPageData = async () => {
      try {
        setLoading(true);
        // In a real app, this would be an API call to the backend
        // const response = await axios.get('/api/about/page-data/');
        // setPageData(response.data);

        // For demo purposes, we'll use the mock data
        setTimeout(() => {
          setPageData({
            team_members: [
              {
                id: 1,
                name: 'Sarah Johnson',
                role: 'Executive Director',
                bio: 'Sarah has over 15 years of experience in the non-profit sector and is passionate about creating sustainable change.',
                avatar: '/images/team/sarah.jpg',
              },
              {
                id: 2,
                name: 'Michael Chen',
                role: 'Director of Operations',
                bio: 'Michael brings his expertise in finance and operations to ensure HopeBridge runs efficiently and effectively.',
                avatar: '/images/team/michael.jpg',
              },
              {
                id: 3,
                name: 'Aisha Patel',
                role: 'Partnerships Manager',
                bio: 'Aisha works closely with our corporate partners to develop meaningful collaborations that drive impact.',
                avatar: '/images/team/aisha.jpg',
              },
              {
                id: 4,
                name: 'David Rodriguez',
                role: 'Technology Lead',
                bio: 'David oversees our technology infrastructure and digital platforms to connect donors with causes worldwide.',
                avatar: '/images/team/david.jpg',
              },
            ],
            testimonials: [
              {
                id: 1,
                quote: "HopeBridge's support has transformed our community. We've been able to build a new school that serves over 500 children.",
                author: "Maria Gonzalez",
                organization: "Education for All, Mexico",
              },
              {
                id: 2,
                quote: "The funding we received through HopeBridge helped us provide clean water to three villages that previously had no access.",
                author: "Emmanuel Okafor",
                organization: "Clean Water Initiative, Nigeria",
              },
              {
                id: 3,
                quote: "Working with HopeBridge has been seamless. Their platform made it easy for us to share our story and connect with donors worldwide.",
                author: "Priya Sharma",
                organization: "Women's Empowerment Collective, India",
              },
            ],
            impact_stats: [
              { id: 1, title: 'Total Donations', value: '$750M+' },
              { id: 2, title: 'Projects Funded', value: '31,000+' },
              { id: 3, title: 'Countries Reached', value: '175+' },
            ],
            mission: "To transform aid and philanthropy to accelerate community-led change.",
            vision: "Unleashing the potential of people to make positive change happen.",
            values: "Always open, committed to listening and learning, focused on community-led change.",
            story: "HopeBridge is the largest global crowdfunding community connecting nonprofits, donors, and companies in nearly every country. We help fellow nonprofits access the funding, tools, training, and support they need to serve their communities.",
          });
          setLoading(false);
        }, 1000);
      } catch (err) {
        setError('Failed to load about page data. Please try again later.');
        setLoading(false);
        console.error('Error fetching about page data:', err);
      }
    };

    fetchAboutPageData();
  }, []);

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '50vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Box sx={{ p: 3 }}>
        <Alert severity="error">{error}</Alert>
      </Box>
    );
  }

  return (
    <>
      {/* Hero Section */}
      <Box sx={{ bgcolor: '#f5f5f5', py: 6, textAlign: 'center' }}>
        <Container maxWidth="md">
          <Typography variant="h3" component="h1" gutterBottom>
            About HopeBridge
          </Typography>
          <Typography variant="h6" color="text.secondary">
            Transforming aid and philanthropy to accelerate community-led change
          </Typography>
        </Container>
      </Box>

      {/* Our Story Section */}
      <Container maxWidth="md" sx={{ py: 8, textAlign: 'center' }}>
        <Typography variant="h4" component="h2" gutterBottom>
          Our Story
        </Typography>
        <Typography variant="body1" paragraph>
          {pageData.story}
        </Typography>
        <Box sx={{ mt: 4 }}>
          <Button 
            component={RouterLink} 
            to="/projects" 
            variant="contained" 
            color="primary"
            size="large"
          >
            Find a Project
          </Button>
        </Box>
      </Container>

      {/* Mission, Vision, Values */}
      <Box sx={{ py: 8, bgcolor: '#f9f9f9' }}>
        <Container maxWidth="lg">
          <Grid container spacing={4}>
            <Grid item xs={12} md={4}>
              <Paper elevation={0} sx={{ p: 3, height: '100%' }}>
                <Typography variant="h5" component="h3" gutterBottom>
                  Our Mission
                </Typography>
                <Typography variant="body1">
                  {pageData.mission}
                </Typography>
              </Paper>
            </Grid>
            <Grid item xs={12} md={4}>
              <Paper elevation={0} sx={{ p: 3, height: '100%' }}>
                <Typography variant="h5" component="h3" gutterBottom>
                  Our Vision
                </Typography>
                <Typography variant="body1">
                  {pageData.vision}
                </Typography>
              </Paper>
            </Grid>
            <Grid item xs={12} md={4}>
              <Paper elevation={0} sx={{ p: 3, height: '100%' }}>
                <Typography variant="h5" component="h3" gutterBottom>
                  Our Values
                </Typography>
                <Typography variant="body1">
                  {pageData.values}
                </Typography>
              </Paper>
            </Grid>
          </Grid>
        </Container>
      </Box>

      {/* Impact Section */}
      <Box sx={{ py: 8, bgcolor: '#e0f7f6' }}>
        <Container maxWidth="lg">
          <Typography variant="h4" component="h2" align="center" gutterBottom>
            Our Impact
          </Typography>
          <Grid container spacing={6} justifyContent="center" sx={{ mt: 2 }}>
            {pageData.impact_stats.map((stat) => (
              <Grid item key={stat.id} xs={12} md={4}>
                <Box sx={{ textAlign: 'center' }}>
                  <Typography variant="h3" component="div" gutterBottom>
                    {stat.value}
                  </Typography>
                  <Typography variant="h6">{stat.title}</Typography>
                </Box>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* Team Section */}
      <Container maxWidth="lg" sx={{ py: 8 }}>
        <Typography variant="h4" component="h2" align="center" gutterBottom>
          Our Team
        </Typography>
        <Grid container spacing={4} sx={{ mt: 2 }}>
          {pageData.team_members.map((member) => (
            <Grid item key={member.id} xs={12} sm={6} md={3}>
              <Card sx={{ height: '100%' }}>
                <Box sx={{ p: 2, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                  <Avatar
                    src={member.avatar}
                    alt={member.name}
                    sx={{ width: 120, height: 120, mb: 2 }}
                  />
                  <Typography variant="h6" component="div" align="center">
                    {member.name}
                  </Typography>
                  <Typography variant="subtitle1" color="text.secondary" align="center" gutterBottom>
                    {member.role}
                  </Typography>
                  <Typography variant="body2" align="center">
                    {member.bio}
                  </Typography>
                </Box>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>

      {/* Corporate Partnerships */}
      <Box sx={{ py: 8, bgcolor: '#f9f9f9' }}>
        <Container maxWidth="lg">
          <Typography variant="h4" component="h2" align="center" gutterBottom>
            Corporate Partnerships
          </Typography>
          <Typography variant="body1" align="center" paragraph sx={{ maxWidth: 800, mx: 'auto' }}>
            We help companies develop powerful corporate social responsibility, grantmaking, and employee engagement strategies.
          </Typography>
          <Box sx={{ textAlign: 'center', mt: 4 }}>
            <Button 
              component={RouterLink} 
              to="/companies" 
              variant="contained" 
              color="primary"
              size="large"
            >
              Learn More
            </Button>
          </Box>
        </Container>
      </Box>

      {/* Testimonials */}
      <Container maxWidth="lg" sx={{ py: 8 }}>
        <Typography variant="h4" component="h2" align="center" gutterBottom>
          Testimonials
        </Typography>
        <Grid container spacing={4} sx={{ mt: 2 }}>
          {pageData.testimonials.map((testimonial) => (
            <Grid item key={testimonial.id} xs={12} md={4}>
              <Card sx={{ height: '100%' }}>
                <CardContent>
                  <Typography variant="body1" paragraph sx={{ fontStyle: 'italic' }}>
                    "{testimonial.quote}"
                  </Typography>
                  <Divider sx={{ my: 2 }} />
                  <Typography variant="subtitle1" component="div">
                    {testimonial.author}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {testimonial.organization}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>
    </>
  );
};

export default AboutPage; 