import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import {
  Box,
  Container,
  Grid,
  Typography,
  Link,
  IconButton,
  Divider,
  Stack,
} from '@mui/material';
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';
import InstagramIcon from '@mui/icons-material/Instagram';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import FavoriteIcon from '@mui/icons-material/Favorite';

const Footer = () => {
  return (
    <Box
      component="footer"
      sx={{
        bgcolor: 'background.paper',
        py: 6,
        borderTop: '1px solid',
        borderColor: 'divider',
      }}
    >
      <Container maxWidth="lg">
        <Grid container spacing={4}>
          <Grid item xs={12} sm={4}>
            <Box display="flex" alignItems="center" mb={2}>
              <FavoriteIcon sx={{ color: 'primary.main', mr: 1 }} />
              <Typography variant="h6" color="text.primary" gutterBottom>
                HopeBridge
              </Typography>
            </Box>
            <Typography variant="body2" color="text.secondary" paragraph>
              Connecting donors with those in need. Together, we can make a difference
              in the lives of people around the world.
            </Typography>
            <Stack direction="row" spacing={1}>
              <IconButton aria-label="facebook" color="inherit">
                <FacebookIcon />
              </IconButton>
              <IconButton aria-label="twitter" color="inherit">
                <TwitterIcon />
              </IconButton>
              <IconButton aria-label="instagram" color="inherit">
                <InstagramIcon />
              </IconButton>
              <IconButton aria-label="linkedin" color="inherit">
                <LinkedInIcon />
              </IconButton>
            </Stack>
          </Grid>
          
          <Grid item xs={12} sm={4}>
            <Typography variant="h6" color="text.primary" gutterBottom>
              Quick Links
            </Typography>
            <Box>
              <Link component={RouterLink} to="/" color="inherit" display="block" sx={{ mb: 1 }}>
                Home
              </Link>
              <Link component={RouterLink} to="/about" color="inherit" display="block" sx={{ mb: 1 }}>
                About Us
              </Link>
              <Link component={RouterLink} to="/campaigns" color="inherit" display="block" sx={{ mb: 1 }}>
                Campaigns
              </Link>
              <Link component={RouterLink} to="/donate" color="inherit" display="block" sx={{ mb: 1 }}>
                Donate
              </Link>
              <Link component={RouterLink} to="/contact" color="inherit" display="block" sx={{ mb: 1 }}>
                Contact
              </Link>
            </Box>
          </Grid>
          
          <Grid item xs={12} sm={4}>
            <Typography variant="h6" color="text.primary" gutterBottom>
              Contact Us
            </Typography>
            <Typography variant="body2" color="text.secondary" paragraph>
              123 Charity Lane
              <br />
              London, UK
              <br />
              <Link href="mailto:info@hopebridge.org" color="inherit">
                info@hopebridge.org
              </Link>
              <br />
              <Link href="tel:+441234567890" color="inherit">
                +44 123 456 7890
              </Link>
            </Typography>
          </Grid>
        </Grid>
        
        <Divider sx={{ my: 4 }} />
        
        <Box textAlign="center">
          <Typography variant="body2" color="text.secondary">
            {'© '}
            {new Date().getFullYear()}
            {' HopeBridge. All rights reserved.'}
          </Typography>
          <Box mt={1}>
            <Link href="/privacy" color="inherit" sx={{ mx: 1 }}>
              Privacy Policy
            </Link>
            <Link href="/terms" color="inherit" sx={{ mx: 1 }}>
              Terms of Service
            </Link>
            <Link href="/cookies" color="inherit" sx={{ mx: 1 }}>
              Cookie Policy
            </Link>
          </Box>
        </Box>
      </Container>
    </Box>
  );
};

export default Footer; 