import React, { useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import {
  Box,
  Button,
  Card,
  CardActionArea,
  CardContent,
  CardMedia,
  Container,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Divider,
  Grid,
  Link,
  Paper,
  TextField,
  Typography,
  useMediaQuery,
  useTheme,
  FormControl,
  FormControlLabel,
  RadioGroup,
  Radio,
  InputAdornment,
} from '@mui/material';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import PeopleIcon from '@mui/icons-material/People';
import PublicIcon from '@mui/icons-material/Public';
import VolunteerActivismIcon from '@mui/icons-material/VolunteerActivism';

// Import axios from our mock in test environment or real axios in production
let axios;
if (process.env.NODE_ENV === 'test') {
  axios = require('../tests/mocks/axios').default;
} else {
  axios = require('axios').default;
}

// Mock data for campaigns (would come from API in real app)
const featuredCampaigns = [
  {
    id: 1,
    title: 'Clean Water Initiative',
    description: 'Providing clean water to communities in need across Africa.',
    image: '/images/campaigns/water.jpg',
    raised: 12500,
    goal: 25000,
    daysLeft: 15,
  },
  {
    id: 2,
    title: 'Education for All',
    description: 'Supporting education for underprivileged children worldwide.',
    image: '/images/campaigns/education.jpg',
    raised: 18000,
    goal: 30000,
    daysLeft: 22,
  },
  {
    id: 3,
    title: 'Emergency Relief Fund',
    description: 'Immediate assistance for communities affected by natural disasters.',
    image: '/images/campaigns/emergency.jpg',
    raised: 45000,
    goal: 50000,
    daysLeft: 5,
  },
];

// Mock data for testimonials
const testimonials = [
  {
    id: 1,
    name: 'Sarah Johnson',
    role: 'Regular Donor',
    quote: 'HopeBridge has made it so easy for me to contribute to causes I care about. I love seeing the direct impact of my donations.',
    avatar: '/images/testimonials/sarah.jpg',
  },
  {
    id: 2,
    name: 'Michael Chen',
    role: 'Volunteer',
    quote: 'Volunteering with HopeBridge has been one of the most rewarding experiences of my life. The team is dedicated and passionate.',
    avatar: '/images/testimonials/michael.jpg',
  },
  {
    id: 3,
    name: 'Aisha Patel',
    role: 'Campaign Beneficiary',
    quote: 'The support from HopeBridge donors helped our community rebuild after the flood. We are forever grateful.',
    avatar: '/images/testimonials/aisha.jpg',
  },
];

const HomePage = () => {
  const [openDonateDialog, setOpenDonateDialog] = useState(false);
  const [donationAmount, setDonationAmount] = useState('25');
  const [customAmount, setCustomAmount] = useState('');
  const [campaigns, setCampaigns] = useState(featuredCampaigns);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  // In a real app, we would fetch campaigns from the API
  useEffect(() => {
    // Example API call (commented out since we're using mock data)
    // const fetchCampaigns = async () => {
    //   try {
    //     const response = await axios.get('/api/campaigns/featured');
    //     setCampaigns(response.data);
    //   } catch (error) {
    //     console.error('Error fetching campaigns:', error);
    //   }
    // };
    // fetchCampaigns();
  }, []);

  const handleOpenDonateDialog = () => {
    setOpenDonateDialog(true);
  };

  const handleCloseDonateDialog = () => {
    setOpenDonateDialog(false);
  };

  const handleDonationAmountChange = (event) => {
    setDonationAmount(event.target.value);
    if (event.target.value === 'custom') {
      setCustomAmount('');
    }
  };

  const handleCustomAmountChange = (event) => {
    setCustomAmount(event.target.value);
  };

  const handleDonateSubmit = () => {
    // In a real app, this would process the donation through Stripe/PayPal
    console.log('Processing donation:', donationAmount === 'custom' ? customAmount : donationAmount);
    handleCloseDonateDialog();
    // Would redirect to payment processing
  };

  return (
    <>
      {/* Hero Section */}
      <Box
        className="hero-section"
        sx={{
          backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("/images/hero-bg.jpg")',
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          color: 'white',
          py: { xs: 8, md: 15 },
          textAlign: 'center',
        }}
      >
        <Container maxWidth="md">
          <Typography
            component="h1"
            variant="h2"
            gutterBottom
            className="fade-in"
            sx={{ fontWeight: 700, mb: 3 }}
          >
            Making a Difference Together
          </Typography>
          <Typography
            variant="h5"
            paragraph
            sx={{ mb: 4, maxWidth: '800px', mx: 'auto' }}
          >
            Join us in our mission to create positive change and bring hope to communities around the world.
          </Typography>
          <Box sx={{ mt: 4 }}>
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={handleOpenDonateDialog}
              sx={{ mr: { xs: 0, sm: 2 }, mb: { xs: 2, sm: 0 } }}
            >
              Donate Now
            </Button>
            <Button
              variant="outlined"
              component={RouterLink}
              to="/campaigns"
              size="large"
              sx={{ backgroundColor: 'rgba(255, 255, 255, 0.1)', color: 'white', borderColor: 'white' }}
            >
              Explore Campaigns
            </Button>
          </Box>
        </Container>
      </Box>

      {/* Impact Statistics */}
      <Box sx={{ py: 8, bgcolor: 'background.paper' }}>
        <Container maxWidth="lg">
          <Grid container spacing={4} justifyContent="center">
            <Grid item xs={12} sm={4} md={4}>
              <Card className="impact-card" elevation={0} sx={{ textAlign: 'center', p: 2 }}>
                <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
                  <PeopleIcon sx={{ fontSize: 60, color: 'primary.main' }} />
                </Box>
                <CardContent className="impact-card-content">
                  <Typography variant="h3" component="div" sx={{ fontWeight: 'bold', mb: 1 }}>
                    50,000+
                  </Typography>
                  <Typography variant="h6" color="text.secondary">
                    People Helped
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={4} md={4}>
              <Card className="impact-card" elevation={0} sx={{ textAlign: 'center', p: 2 }}>
                <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
                  <PublicIcon sx={{ fontSize: 60, color: 'primary.main' }} />
                </Box>
                <CardContent className="impact-card-content">
                  <Typography variant="h3" component="div" sx={{ fontWeight: 'bold', mb: 1 }}>
                    25+
                  </Typography>
                  <Typography variant="h6" color="text.secondary">
                    Countries Reached
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={4} md={4}>
              <Card className="impact-card" elevation={0} sx={{ textAlign: 'center', p: 2 }}>
                <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
                  <VolunteerActivismIcon sx={{ fontSize: 60, color: 'primary.main' }} />
                </Box>
                <CardContent className="impact-card-content">
                  <Typography variant="h3" component="div" sx={{ fontWeight: 'bold', mb: 1 }}>
                    $2.5M+
                  </Typography>
                  <Typography variant="h6" color="text.secondary">
                    Funds Raised
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Container>
      </Box>

      {/* Featured Campaigns */}
      <Box sx={{ py: 8, bgcolor: 'background.default' }}>
        <Container maxWidth="lg">
          <Typography
            component="h2"
            variant="h3"
            align="center"
            gutterBottom
            sx={{ mb: 6 }}
          >
            Featured Campaigns
          </Typography>
          <Grid container spacing={4}>
            {campaigns.map((campaign) => (
              <Grid item key={campaign.id} xs={12} sm={6} md={4}>
                <Card className="campaign-card" sx={{ height: '100%' }}>
                  <CardActionArea component={RouterLink} to={`/campaigns/${campaign.id}`}>
                    <CardMedia
                      component="img"
                      height="200"
                      image={campaign.image || 'https://source.unsplash.com/random?charity'}
                      alt={campaign.title}
                    />
                    <CardContent className="campaign-card-content">
                      <Typography gutterBottom variant="h5" component="div">
                        {campaign.title}
                      </Typography>
                      <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                        {campaign.description}
                      </Typography>
                      <Box sx={{ mt: 2 }}>
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                          <Typography variant="body2" color="text.secondary">
                            Raised: ${campaign.raised.toLocaleString()}
                          </Typography>
                          <Typography variant="body2" color="text.secondary">
                            Goal: ${campaign.goal.toLocaleString()}
                          </Typography>
                        </Box>
                        <Box
                          sx={{
                            width: '100%',
                            height: 8,
                            bgcolor: 'grey.200',
                            borderRadius: 5,
                            overflow: 'hidden',
                          }}
                        >
                          <Box
                            sx={{
                              width: `${(campaign.raised / campaign.goal) * 100}%`,
                              height: '100%',
                              bgcolor: 'primary.main',
                            }}
                          />
                        </Box>
                        <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 1 }}>
                          <Typography variant="body2" color="text.secondary">
                            {campaign.daysLeft} days left
                          </Typography>
                        </Box>
                      </Box>
                    </CardContent>
                  </CardActionArea>
                </Card>
              </Grid>
            ))}
          </Grid>
          <Box sx={{ textAlign: 'center', mt: 4 }}>
            <Button
              component={RouterLink}
              to="/campaigns"
              variant="outlined"
              color="primary"
              endIcon={<ArrowForwardIcon />}
            >
              View All Campaigns
            </Button>
          </Box>
        </Container>
      </Box>

      {/* Success Stories / Testimonials */}
      <Box sx={{ py: 8, bgcolor: 'background.paper' }}>
        <Container maxWidth="lg">
          <Typography
            component="h2"
            variant="h3"
            align="center"
            gutterBottom
            sx={{ mb: 6 }}
          >
            Success Stories
          </Typography>
          <Grid container spacing={4}>
            {testimonials.map((testimonial) => (
              <Grid item key={testimonial.id} xs={12} md={4}>
                <Card className="testimonial-card" sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography variant="body1" paragraph sx={{ fontStyle: 'italic', mb: 3 }}>
                      "{testimonial.quote}"
                    </Typography>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <Box
                        component="img"
                        src={testimonial.avatar || 'https://source.unsplash.com/random?person'}
                        alt={testimonial.name}
                        sx={{
                          width: 60,
                          height: 60,
                          borderRadius: '50%',
                          mr: 2,
                        }}
                      />
                      <Box>
                        <Typography variant="subtitle1" sx={{ fontWeight: 'bold' }}>
                          {testimonial.name}
                        </Typography>
                        <Typography variant="body2" color="text.secondary">
                          {testimonial.role}
                        </Typography>
                      </Box>
                    </Box>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* Call to Action */}
      <Box
        sx={{
          py: 8,
          bgcolor: 'primary.main',
          color: 'white',
          textAlign: 'center',
        }}
      >
        <Container maxWidth="md">
          <Typography variant="h3" gutterBottom>
            Ready to Make a Difference?
          </Typography>
          <Typography variant="h6" paragraph sx={{ mb: 4 }}>
            Your support can change lives. Join us today in our mission to create a better world.
          </Typography>
          <Box>
            <Button
              variant="contained"
              color="secondary"
              size="large"
              onClick={handleOpenDonateDialog}
              sx={{ mr: { xs: 0, sm: 2 }, mb: { xs: 2, sm: 0 } }}
            >
              Donate Now
            </Button>
            <Button
              variant="outlined"
              component={RouterLink}
              to="/contact"
              size="large"
              sx={{ color: 'white', borderColor: 'white' }}
            >
              Get Involved
            </Button>
          </Box>
        </Container>
      </Box>

      {/* Donation Dialog */}
      <Dialog
        open={openDonateDialog}
        onClose={handleCloseDonateDialog}
        aria-labelledby="donation-dialog-title"
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle id="donation-dialog-title">Make a Donation</DialogTitle>
        <DialogContent>
          <DialogContentText sx={{ mb: 3 }}>
            Your generosity helps us continue our mission. Choose an amount to donate:
          </DialogContentText>
          <FormControl component="fieldset" sx={{ width: '100%', mb: 3 }}>
            <RadioGroup
              aria-label="donation-amount"
              name="donation-amount"
              value={donationAmount}
              onChange={handleDonationAmountChange}
            >
              <Grid container spacing={2}>
                <Grid item xs={6} sm={3}>
                  <FormControlLabel
                    value="10"
                    control={<Radio />}
                    label="$10"
                    sx={{ width: '100%' }}
                  />
                </Grid>
                <Grid item xs={6} sm={3}>
                  <FormControlLabel
                    value="25"
                    control={<Radio />}
                    label="$25"
                    sx={{ width: '100%' }}
                  />
                </Grid>
                <Grid item xs={6} sm={3}>
                  <FormControlLabel
                    value="50"
                    control={<Radio />}
                    label="$50"
                    sx={{ width: '100%' }}
                  />
                </Grid>
                <Grid item xs={6} sm={3}>
                  <FormControlLabel
                    value="100"
                    control={<Radio />}
                    label="$100"
                    sx={{ width: '100%' }}
                  />
                </Grid>
                <Grid item xs={12}>
                  <FormControlLabel
                    value="custom"
                    control={<Radio />}
                    label="Custom Amount"
                    sx={{ width: '100%' }}
                  />
                </Grid>
              </Grid>
            </RadioGroup>
          </FormControl>

          {donationAmount === 'custom' && (
            <TextField
              autoFocus
              margin="dense"
              id="custom-amount"
              label="Enter amount"
              type="number"
              fullWidth
              variant="outlined"
              value={customAmount}
              onChange={handleCustomAmountChange}
              InputProps={{
                startAdornment: <InputAdornment position="start">$</InputAdornment>,
              }}
            />
          )}

          <TextField
            margin="dense"
            id="name"
            label="Full Name"
            type="text"
            fullWidth
            variant="outlined"
            sx={{ mt: 3 }}
          />
          <TextField
            margin="dense"
            id="email"
            label="Email Address"
            type="email"
            fullWidth
            variant="outlined"
          />
        </DialogContent>
        <DialogActions sx={{ px: 3, pb: 3 }}>
          <Button onClick={handleCloseDonateDialog}>Cancel</Button>
          <Button
            onClick={handleDonateSubmit}
            variant="contained"
            color="primary"
            disabled={donationAmount === 'custom' && !customAmount}
          >
            Proceed to Payment
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default HomePage; 