import React, { useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardActionArea,
  CardContent,
  CardMedia,
  Button,
  TextField,
  InputAdornment,
  MenuItem,
  FormControl,
  Select,
  InputLabel,
  Pagination,
  Chip,
  LinearProgress,
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import FilterListIcon from '@mui/icons-material/FilterList';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import AccessTimeIcon from '@mui/icons-material/AccessTime';

// Mock data for campaigns
const mockCampaigns = [
  {
    id: 1,
    title: 'Clean Water Initiative',
    description: 'Providing clean water to communities in need across Africa.',
    image: '/images/campaigns/water.jpg',
    raised: 12500,
    goal: 25000,
    daysLeft: 15,
    location: 'Kenya',
    category: 'health',
  },
  {
    id: 2,
    title: 'Education for All',
    description: 'Supporting education for underprivileged children worldwide.',
    image: '/images/campaigns/education.jpg',
    raised: 18000,
    goal: 30000,
    daysLeft: 22,
    location: 'India',
    category: 'education',
  },
  {
    id: 3,
    title: 'Emergency Relief Fund',
    description: 'Immediate assistance for communities affected by natural disasters.',
    image: '/images/campaigns/emergency.jpg',
    raised: 45000,
    goal: 50000,
    daysLeft: 5,
    location: 'Philippines',
    category: 'disaster',
  },
  {
    id: 4,
    title: 'Sustainable Farming Project',
    description: 'Teaching sustainable farming techniques to rural communities.',
    image: '/images/campaigns/farming.jpg',
    raised: 8000,
    goal: 20000,
    daysLeft: 30,
    location: 'Brazil',
    category: 'environment',
  },
  {
    id: 5,
    title: 'Medical Supplies for Clinics',
    description: 'Providing essential medical supplies to understaffed clinics.',
    image: '/images/campaigns/medical.jpg',
    raised: 15000,
    goal: 40000,
    daysLeft: 18,
    location: 'Uganda',
    category: 'health',
  },
  {
    id: 6,
    title: 'Women Empowerment Initiative',
    description: 'Supporting women entrepreneurs in developing countries.',
    image: '/images/campaigns/women.jpg',
    raised: 22000,
    goal: 35000,
    daysLeft: 25,
    location: 'Bangladesh',
    category: 'social',
  },
];

const categories = [
  { value: 'all', label: 'All Categories' },
  { value: 'health', label: 'Health & Wellness' },
  { value: 'education', label: 'Education' },
  { value: 'disaster', label: 'Disaster Relief' },
  { value: 'environment', label: 'Environment' },
  { value: 'social', label: 'Social Causes' },
];

const sortOptions = [
  { value: 'newest', label: 'Newest First' },
  { value: 'endingSoon', label: 'Ending Soon' },
  { value: 'mostFunded', label: 'Most Funded' },
  { value: 'leastFunded', label: 'Least Funded' },
];

const CampaignsPage = () => {
  const [campaigns, setCampaigns] = useState([]);
  const [filteredCampaigns, setFilteredCampaigns] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [category, setCategory] = useState('all');
  const [sortBy, setSortBy] = useState('newest');
  const [page, setPage] = useState(1);
  const campaignsPerPage = 6;

  // Fetch campaigns (using mock data for now)
  useEffect(() => {
    // Using mock data
    setTimeout(() => {
      setCampaigns(mockCampaigns);
      setFilteredCampaigns(mockCampaigns);
      setLoading(false);
    }, 500);
  }, []);

  // Filter and sort campaigns
  useEffect(() => {
    let result = [...campaigns];

    // Filter by search term
    if (searchTerm) {
      result = result.filter(
        (campaign) =>
          campaign.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
          campaign.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
          campaign.location.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    // Filter by category
    if (category !== 'all') {
      result = result.filter((campaign) => campaign.category === category);
    }

    // Sort
    switch (sortBy) {
      case 'endingSoon':
        result.sort((a, b) => a.daysLeft - b.daysLeft);
        break;
      case 'mostFunded':
        result.sort((a, b) => (b.raised / b.goal) - (a.raised / a.goal));
        break;
      case 'leastFunded':
        result.sort((a, b) => (a.raised / a.goal) - (b.raised / b.goal));
        break;
      case 'newest':
      default:
        // Assuming newer campaigns have higher IDs
        result.sort((a, b) => b.id - a.id);
        break;
    }

    setFilteredCampaigns(result);
    setPage(1); // Reset to first page when filters change
  }, [campaigns, searchTerm, category, sortBy]);

  // Calculate pagination
  const totalPages = Math.ceil(filteredCampaigns.length / campaignsPerPage);
  const displayedCampaigns = filteredCampaigns.slice(
    (page - 1) * campaignsPerPage,
    page * campaignsPerPage
  );

  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value);
  };

  const handleCategoryChange = (event) => {
    setCategory(event.target.value);
  };

  const handleSortChange = (event) => {
    setSortBy(event.target.value);
  };

  const handlePageChange = (event, value) => {
    setPage(value);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  return (
    <>
      {/* Hero Section */}
      <Box
        sx={{
          bgcolor: 'primary.main',
          color: 'white',
          py: 6,
          textAlign: 'center',
        }}
      >
        <Container maxWidth="md">
          <Typography variant="h2" component="h1" gutterBottom>
            Our Campaigns
          </Typography>
          <Typography variant="h6" sx={{ mb: 4 }}>
            Discover causes that need your support and make a difference today.
          </Typography>
        </Container>
      </Box>

      {/* Filters Section */}
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Grid container spacing={2} alignItems="center" sx={{ mb: 4 }}>
          <Grid item xs={12} md={6}>
            <TextField
              fullWidth
              placeholder="Search campaigns..."
              value={searchTerm}
              onChange={handleSearchChange}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon />
                  </InputAdornment>
                ),
              }}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <FormControl fullWidth>
              <InputLabel id="category-label">Category</InputLabel>
              <Select
                labelId="category-label"
                id="category-select"
                value={category}
                label="Category"
                onChange={handleCategoryChange}
                startAdornment={
                  <InputAdornment position="start">
                    <FilterListIcon />
                  </InputAdornment>
                }
              >
                {categories.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <FormControl fullWidth>
              <InputLabel id="sort-label">Sort By</InputLabel>
              <Select
                labelId="sort-label"
                id="sort-select"
                value={sortBy}
                label="Sort By"
                onChange={handleSortChange}
              >
                {sortOptions.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
        </Grid>

        {/* Results count */}
        <Box sx={{ mb: 3 }}>
          <Typography variant="body2" color="text.secondary">
            Showing {filteredCampaigns.length} {filteredCampaigns.length === 1 ? 'campaign' : 'campaigns'}
            {category !== 'all' && ` in ${categories.find((c) => c.value === category)?.label}`}
            {searchTerm && ` matching "${searchTerm}"`}
          </Typography>
        </Box>

        {/* Campaigns Grid */}
        {loading ? (
          <Box sx={{ width: '100%', mt: 4 }}>
            <LinearProgress />
          </Box>
        ) : displayedCampaigns.length === 0 ? (
          <Box sx={{ textAlign: 'center', py: 8 }}>
            <Typography variant="h5" color="text.secondary" gutterBottom>
              No campaigns found
            </Typography>
            <Typography variant="body1" color="text.secondary">
              Try adjusting your search or filters to find what you're looking for.
            </Typography>
          </Box>
        ) : (
          <Grid container spacing={4}>
            {displayedCampaigns.map((campaign) => (
              <Grid item key={campaign.id} xs={12} sm={6} md={4}>
                <Card className="campaign-card" sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
                  <CardActionArea component={RouterLink} to={`/campaigns/${campaign.id}`}>
                    <CardMedia
                      component="img"
                      height="200"
                      image={campaign.image || 'https://source.unsplash.com/random?charity'}
                      alt={campaign.title}
                    />
                    <CardContent sx={{ flexGrow: 1 }}>
                      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                        <Chip
                          size="small"
                          label={categories.find((c) => c.value === campaign.category)?.label}
                          color="primary"
                          variant="outlined"
                        />
                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                          <AccessTimeIcon sx={{ fontSize: 16, mr: 0.5, color: 'text.secondary' }} />
                          <Typography variant="caption" color="text.secondary">
                            {campaign.daysLeft} days left
                          </Typography>
                        </Box>
                      </Box>
                      <Typography gutterBottom variant="h5" component="h2">
                        {campaign.title}
                      </Typography>
                      <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                        {campaign.description}
                      </Typography>
                      <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                        <LocationOnIcon sx={{ fontSize: 16, mr: 0.5, color: 'text.secondary' }} />
                        <Typography variant="caption" color="text.secondary">
                          {campaign.location}
                        </Typography>
                      </Box>
                      <Box sx={{ mt: 'auto' }}>
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                          <Typography variant="body2" color="text.secondary">
                            ${campaign.raised.toLocaleString()} raised
                          </Typography>
                          <Typography variant="body2" color="text.secondary">
                            ${campaign.goal.toLocaleString()} goal
                          </Typography>
                        </Box>
                        <LinearProgress
                          variant="determinate"
                          value={(campaign.raised / campaign.goal) * 100}
                          sx={{ height: 8, borderRadius: 5 }}
                        />
                      </Box>
                    </CardContent>
                  </CardActionArea>
                </Card>
              </Grid>
            ))}
          </Grid>
        )}

        {/* Pagination */}
        {totalPages > 1 && (
          <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
            <Pagination
              count={totalPages}
              page={page}
              onChange={handlePageChange}
              color="primary"
              size="large"
            />
          </Box>
        )}

        {/* Start a Campaign CTA */}
        <Box
          sx={{
            mt: 8,
            p: 4,
            textAlign: 'center',
            bgcolor: 'background.paper',
            borderRadius: 2,
            boxShadow: 1,
          }}
        >
          <Typography variant="h4" gutterBottom>
            Have a Cause You Care About?
          </Typography>
          <Typography variant="body1" paragraph sx={{ maxWidth: 700, mx: 'auto', mb: 3 }}>
            Start your own campaign and rally support for a cause that matters to you.
            Our platform makes it easy to create, share, and manage your fundraising efforts.
          </Typography>
          <Button variant="contained" color="primary" size="large">
            Start a Campaign
          </Button>
        </Box>
      </Container>
    </>
  );
};

export default CampaignsPage; 